'use client';

import { useState } from 'react';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { vscDarkPlus } from 'react-syntax-highlighter/dist/esm/styles/prism';

export default function Home() {
  const [input, setInput] = useState('');
  const [completion, setCompletion] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [language, setLanguage] = useState('javascript');

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setCompletion('');
    setIsLoading(true);

    try {
      const response = await fetch('/api/explain', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ 
          prompt: input,
          language: language 
        }),
      });

      if (!response.ok) {
        throw new Error('Failed to generate explanation');
      }

      const reader = response.body?.getReader();
      if (!reader) {
        throw new Error('No reader available');
      }

      const decoder = new TextDecoder();
      while (true) {
        const { done, value } = await reader.read();
        if (done) break;
        
        const text = decoder.decode(value);
        setCompletion(prev => prev + text);
      }
    } catch (error) {
      console.error('Error:', error);
      setCompletion('Error generating explanation. Please try again.');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <main className="min-h-screen p-8 bg-gray-50">
      <div className="max-w-6xl mx-auto">
        <h1 className="text-4xl font-bold mb-8 text-center text-gray-800">CodeCanvas</h1>
        <p className="text-center text-gray-600 mb-8">
          Get AI-powered explanations for your code with visual analogies
        </p>
        
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          {/* Code Input Section */}
          <div className="space-y-4">
            <div className="flex justify-between items-center">
              <h2 className="text-2xl font-semibold text-gray-800">Your Code</h2>
              <select
                value={language}
                onChange={(e) => setLanguage(e.target.value)}
                className="px-3 py-1 border rounded-lg bg-white text-gray-700"
                disabled={isLoading}
              >
                <option value="javascript">JavaScript</option>
                <option value="python">Python</option>
                <option value="typescript">TypeScript</option>
                <option value="java">Java</option>
                <option value="cpp">C++</option>
              </select>
            </div>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div className="relative">
                <SyntaxHighlighter
                  language={language}
                  style={vscDarkPlus}
                  customStyle={{
                    margin: 0,
                    padding: '1rem',
                    height: '24rem',
                    borderRadius: '0.5rem',
                    fontSize: '0.875rem',
                  }}
                  showLineNumbers
                >
                  {input || '// Paste your code here...'}
                </SyntaxHighlighter>
                <textarea
                  value={input}
                  onChange={(e) => setInput(e.target.value)}
                  className="absolute inset-0 w-full h-full opacity-0 cursor-text"
                  placeholder="Paste your code here..."
                />
              </div>
              <button
                type="submit"
                className="w-full bg-blue-500 text-white py-3 px-4 rounded-lg hover:bg-blue-600 transition-colors disabled:bg-blue-300 font-medium"
                disabled={isLoading || !input.trim()}
              >
                {isLoading ? 'Generating...' : 'Generate Explanation'}
              </button>
            </form>
          </div>

          {/* Explanation Section */}
          <div className="space-y-4">
            <h2 className="text-2xl font-semibold text-gray-800">AI Explanation</h2>
            <div className="p-6 border rounded-lg min-h-[24rem] bg-white shadow-sm">
              {isLoading && !completion ? (
                <div className="flex items-center justify-center h-full">
                  <div className="animate-pulse text-gray-500">Generating explanation...</div>
                </div>
              ) : completion ? (
                <div className="prose max-w-none">
                  {completion.split('\n').map((line, i) => (
                    <p key={i} className="mb-2">{line}</p>
                  ))}
                </div>
              ) : (
                <div className="flex items-center justify-center h-full">
                  <p className="text-gray-500 text-center">
                    Your explanation will appear here...<br />
                    Try pasting some code and clicking &quot;Generate Explanation&quot;
                  </p>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </main>
  );
}
